import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Menu from "@mui/material/Menu";
import MenuIcon from "@mui/icons-material/Menu";
import Container from "@mui/material/Container";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import Tooltip from "@mui/material/Tooltip";
import MenuItem from "@mui/material/MenuItem";
import AdbIcon from "@mui/icons-material/Adb";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { ArrowForward, ArrowForwardIos, ExpandLess } from "@mui/icons-material";
import { Stack, useMediaQuery } from "@mui/material";

import BorderImage from "../../../assets/borderImage.png";

const pages: string[] = [
  "Home",
  "About Us",
  "How it Works",
  "In-Sight",
  "Pricing",
  "Contact Us",
];

function ResponsiveAppBar() {
  const [anchorElNav, setAnchorElNav] = React.useState<null | HTMLElement>(
    null
  );
  const [anchorElUser, setAnchorElUser] = React.useState<null | HTMLElement>(
    null
  );
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const matches = useMediaQuery("(max-width:600px)");
  const handleOpenNavMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };
  function CustomMenuItem({
    title,
    body,
    img,
    onClick,
  }: {
    title: string;
    body: string;
    img: string;
    onClick: () => void;
  }) {
    return (
      <>
        {matches ? (
          <MenuItem onClick={handleClose}>{title}</MenuItem>
        ) : (
          <MenuItem onClick={handleClose}>
            <Stack direction={"row"} spacing={2}>
              <Box component={"img"} src={img} height={50} width={50} />
              <Box>
                <Typography
                  sx={{
                    fontSize: "14px",
                    fontWeight: 700,
                  }}
                >
                  {title}
                </Typography>
                <Typography fontSize={"12px"} color={"#84858D"}>
                  {body}
                </Typography>
                <Box
                  width={"105%"}
                  py={1}
                  borderBottom={"1px solid #E5E5EF "}
                />
              </Box>
            </Stack>
          </MenuItem>
        )}
      </>
    );
  }
  return (
    <AppBar
      sx={{
        boxShadow: "none",
        backgroundColor: "white",
        paddingRight: { xs: 0 },
      }}
      position="static"
    >
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Box
            component={"img"}
            sx={{
              width: { md: "70px", xs: "50px" },
              paddingLeft: { xs: "0px", md: "12%" },
            }}
            alt="logo"
            src={require("../../../assets/website-logo.png")}
            // style={{ paddingLeft: "100px" }} // Add padding or margin here
          />

          <AdbIcon sx={{ display: { xs: "flex", md: "none" }, mr: 1 }} />
          <Typography
            variant="h5"
            noWrap
            component="a"
            href="#app-bar-with-responsive-menu"
            sx={{
              mr: 2,
              display: { xs: "flex", md: "none" },
              flexGrow: 1,
              fontFamily: "monospace",
              fontWeight: 700,
              letterSpacing: ".3rem",
              color: "inherit",
              textDecoration: "none",
            }}
          >
            LOGO
          </Typography>
          <Box
            sx={{
              flexGrow: 0.5,
              display: {
                xs: "none",
                md: "flex",
                paddingLeft: "100px",
              },
            }}
          >
            {pages.map((page) => (
              <Button
                endIcon={
                  page == "In-Sight" ? (
                    anchorEl ? (
                      <ExpandLess />
                    ) : (
                      <ExpandMoreIcon />
                    )
                  ) : null
                }
                key={page}
                onClick={(event: React.MouseEvent<HTMLButtonElement>) => {
                  if (page.toLowerCase() === "in-sight") {
                    setAnchorEl(event.currentTarget);
                  }
                }}
                sx={{
                  my: 2,
                  color: "#84858D",
                  mx: 1,
                  textTransform: "none",
                  "&:hover": {
                    color: "#DE9300",
                    backgroundColor: "transparent",
                  },
                }}
              >
                <Typography fontSize={"14px"}>{page}</Typography>
              </Button>
            ))}
            {anchorEl && (
              <Box display={"block"}>
                <Box
                  component={"img"}
                  src={BorderImage}
                  width={50}
                  height={10}
                  marginRight={"auto"}
                  position={"relative"}
                  // position={"relative"}
                  top={34}
                  right={230}
                />
                <Menu
                  id="basic-menu"
                  anchorEl={anchorEl}
                  open={open}
                  onClose={handleClose}
                  MenuListProps={{
                    "aria-labelledby": "basic-button",
                  }}
                  PaperProps={{
                    anchorOrigin: {
                      vertical: "center",
                      horizontal: "center",
                    },
                    transformOrigin: {
                      vertical: "center",
                      horizontal: "center",
                    },
                    sx: {
                      borderTop: matches ? 0 : "1px solid #FFC000", // Set border color
                      // boxShadow: "0px 3px 6px #00000029", // Optional shadow
                    },
                  }}
                >
                  <CustomMenuItem
                    onClick={handleClose}
                    img={require("../../../assets/sight_1.png")}
                    title="Benefits"
                    body="Ea pro tibique comprehensam, sed ea verear"
                  />
                  <CustomMenuItem
                    onClick={handleClose}
                    img={require("../../../assets/sight_2.png")}
                    title="Features"
                    body="Ea pro tibique comprehensam, sed ea verear"
                  />
                  <CustomMenuItem
                    onClick={handleClose}
                    img={require("../../../assets/sight_3.png")}
                    title="Showcase"
                    body="Ea pro tibique comprehensam, sed ea verear"
                  />
                  <CustomMenuItem
                    onClick={handleClose}
                    img={require("../../../assets/sight_4.png")}
                    title="FAQ's"
                    body="Ea pro tibique comprehensam, sed ea verear"
                  />
                </Menu>
              </Box>
            )}
          </Box>
          <Stack direction={"row"} spacing={1} alignItems={"center"}>
            {/* <Box
              sx={{
                width:'20%',
                heigh
               
                objectFit: "contain",
                display: { xs: "none", md: "block" },
              }}
              component={"img"}
              src={require("../../../assets/country.png")}
            /> */}
            <Avatar
              sx={{ height: "20px", width: "20px" }}
              variant="rounded"
              src={require("../../../assets/country.png")}
            />

            <Typography
              sx={{ display: { xs: "none", md: "block" } }}
              color={"black"}
              fontSize={"12px"}
            >
              ENG
            </Typography>
            <ExpandMoreIcon
              sx={{
                position: "relative",
                color: "#84858D",
                display: { xs: "none", md: "block" },
              }}
              fontSize="small"
            />

            <Button>
              <Typography
                sx={{
                  color: "#84858D",
                  textTransform: "none",
                  fontWeight: 800,
                  isplay: { xs: "none", md: "block" },
                  fontSize: "13px",
                }}
              >
                Login
              </Typography>{" "}
            </Button>
          </Stack>
          <Button
            variant="contained"
            sx={{
              backgroundColor: "#FFC000",
              boxShadow: "none",
              color: "black",
              textTransform: "none",
              borderRadius: "5px",
              fontWeight: 700,
              fontSize: { xs: "10px", md: "14px" },
              py: 1.1,
              px: 1.5,
            }}
            endIcon={
              matches ? null : (
                <ArrowForwardIos sx={{ height: 14, width: 14, mt: 0.5 }} />
              )
            }
          >
            Sign up
          </Button>
          <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              sx={{ color: "black" }}
              edge="end"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "left",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: "block", md: "none" },
              }}
            >
              {pages.map((page) => (
                <MenuItem
                  key={page}
                  onClick={(event: any) => {
                    if (page.toLowerCase() === "in-sight") {
                      setAnchorEl(event.currentTarget);
                    }
                    //? function to close the menu
                    // handleCloseNavMenu
                  }}
                >
                  <Typography textAlign="center">{page}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          <Box sx={{ flexGrow: 0 }}>
            {/* <Tooltip title="Open settings">
              <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                <Avatar alt="Remy Sharp" src="/static/images/avatar/2.jpg" />
              </IconButton>
            </Tooltip> */}
            <Menu
              sx={{ mt: "45px" }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >
              {/* {settings.map((setting) => (
                <MenuItem key={setting} onClick={handleCloseUserMenu}>
                  <Typography textAlign="center">{setting}</Typography>
                </MenuItem>
              ))} */}
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
export default ResponsiveAppBar;
